module.exports.Problem = React.createClass({displayName: 'Problem',
	mixins: [backboneModel],
	componentDidMount: function () {
	},
	render: function () {
		function gotoPost () {
			if (window.user)
				app.navigate(post.path, {trigger:true});
			else
				app.flash.info("Entre para visualizar e resolver esse problema.")
		}

		var post = this.props.model.attributes;

		function GenTagList () {
			if (post.subject && post.subject in pageMap) {
				var pageName = pageMap[post.subject].name;
				var subtagsUniverse = pageMap[post.subject].topics || {};
				var tags = [];

				// Populate tags
				tags.push(_.extend(pageMap[post.subject], { id: post.subject }));
				// console.log(post.topic, subtagsUniverse)

				if (post.topic) {
					if (found = _.find(subtagsUniverse, function (i) { return i.id === post.topic })) {
						tags.push(found);
					}
				}

				return (
					React.createElement("div", {className: "tags"}, 
						_.map(tags, function (obj) {
							return (
								React.createElement("div", {className: "tag tag-bg", key: obj.id, 'data-tag': obj.id}, 
									"#", obj.name
								)
							);
						}), 
						React.createElement("div", {className: "tag tag-bg", 'data-tag': "level"+post.level}, 
							"Nível ", post.level
						)
					)
				);
			}
			return null;
		}

		function GenParticipations () {
			var participations = (post.participations || []).slice();
			if (!_.find(participations, function (i) { return i.user.id === post.author.id })) {
				participations.push({
					user: post.author,
					count: 1
				})
			}
			participations = _.unique(participations, function (i) { return i.user.id });
			return _.map(participations.slice(0, 6), function (one) {
				return (
					React.createElement("div", {className: "user-avatar", key: one.user.id, title: one.user.name, 'data-container': "body"}, 
						React.createElement("div", {className: "avatar", style: { backgroundImage: 'url('+one.user.avatarUrl+')'}})
					)
				);
			});
		}

		var thumbnail = post.content.link_image || post.content.cover || post.author.avatarUrl;

		return (
			React.createElement("div", {className: "vcard", onClick: gotoPost, 
				'data-liked': this.props.model.liked, 
				'data-watching': this.props.model.watching}, 
				React.createElement("div", {className: "left"}, 
					React.createElement("div", {className: "thumbnail", style: { backgroundImage: 'url('+thumbnail+')'}}), 
					React.createElement("div", {className: "backdrop"}), 
					React.createElement("div", {className: "over"}, 
						React.createElement("div", null, 
							
								this.props.model.liked?
								React.createElement("i", {className: "icon-thumb-up icon-orange"})
								:React.createElement("i", {className: "icon-thumb-up"}), 
							
							React.createElement("span", {className: "count"}, post.counts.votes)
						)
					)
				), 
				React.createElement("div", {className: "right"}, 
					React.createElement("div", {className: "header"}, 
						React.createElement("div", {className: "title"}, 
							post.content.title
						), 
						React.createElement("div", {className: "info"}, 
							React.createElement("a", {href: post.author.path, className: "author"}, 
								post.author.name
							), 
							React.createElement("i", {className: "icon-dot"}), 
							React.createElement("time", {'data-time-count': 1*new Date(post.created_at), 'data-short': "false", title: formatFullDate(new Date(post.created_at))}, 
								window.calcTimeFrom(post.created_at, false)
							)
						)
					), 
					React.createElement("div", {className: "body"}, 
						extractTextFromMarkdown(post.content.cardBody || '')
					), 
					React.createElement("div", {className: "footer"}, 
						React.createElement("ul", null, 
							React.createElement("div", {className: "stats"}
							), 
							GenTagList()
						), 
						React.createElement("ul", {className: "right"}
						)
					)
				)
			)
		);
							// <div className="participations">
							// 	<span className="count">{post.counts.children}</span>
							// 	<i className="icon-insert-comment"></i>
							// 	{GenParticipations()}
							// </div>
	}
});

module.exports.Post = React.createClass({displayName: 'Post',
	mixins: [backboneModel],
	componentDidMount: function () {
	},
	render: function () {
		function gotoPost () {
			app.navigate(post.path, {trigger:true});
			// if (window.user)
			// else
			// 	window.location.href = post.path;
		}

		var post = this.props.model.attributes;

		function GenTagList () {
			if (post.lab && post.lab in pageMap) {
				var pageName = pageMap[post.lab].name;
				var subtagsUniverse = pageMap[post.lab].children || {};
				var tags = [];

				// Populate tags
				tags.push(_.extend(pageMap[post.lab], { id: post.lab }));
				_.each(post.tags, function (id) {
					if (id in subtagsUniverse)
						tags.push(_.extend(subtagsUniverse[id], { id: id }));
				});

				return (
					React.createElement("div", {className: "tags"}, 
						_.map(tags, function (obj) {
							return (
								React.createElement("div", {className: "tag tag-bg", key: obj.id, 'data-tag': obj.id}, 
									"#", obj.name
								)
							);
						})
					)
				);
			}
			return null;
		}

		function GenParticipations () {
			// var l = _.find(post.participations, function (i) { return i.user.id === post.author.id })
			// console.log(l)

			var participations = (post.participations || []).slice();
			// if (!_.find(participations, function (i) { return i.user.id === post.author.id })) {
			// 	participations.push({
			// 		user: post.author,
			// 		count: 1
			// 	})
			// }
			participations = _.unique(participations, function (i) { return i.user.id });
			return _.map(participations.slice(0, 6), function (one) {
				return (
					React.createElement("div", {className: "user-avatar", key: one.user.id, title: one.user.name, 'data-container': "body"}, 
						React.createElement("div", {className: "avatar", style: { backgroundImage: 'url('+one.user.avatarUrl+')'}})
					)
				);
			});
		}

		if (window.conf && window.conf.lastAccess) {
			// console.log(new Date(window.conf.lastAccess), post.created_at)
			if (new Date(window.conf.lastAccess) < new Date(post.created_at))
				var blink = true;
		}


		var thumbnail = post.content.link_image || post.content.cover || post.author.avatarUrl;

		var before, after;
		if (false && (post.content.link_image || post.content.cover)) {
			before = (
				React.createElement("div", {className: "left"}, 
					React.createElement("div", {className: "thumbnail", style: { backgroundImage: 'url('+thumbnail+')'}})
				)
			);
		} else {
			after = (
				React.createElement("div", {className: "left"}, 
					React.createElement("div", {className: "thumbnail", style: { backgroundImage: 'url('+thumbnail+')'}})
				)
			);
		}

		return (
			React.createElement("div", {className: "vcard "+(blink?"blink":null), onClick: gotoPost, 
				'data-liked': this.props.model.liked, 
				'data-liked': this.props.model.liked, 
				'data-watching': this.props.model.watching}, 
				before, 
				React.createElement("div", {className: "right"}, 
					React.createElement("div", {className: "header"}, 
						React.createElement("div", {className: "title"}, 
							post.content.title
						), 
						React.createElement("div", {className: "info"}, 
							React.createElement("span", {className: "author"}, 
								post.author.name
							), 
							React.createElement("i", {className: "icon-dot"}), 
							React.createElement("time", {'data-time-count': 1*new Date(post.created_at), 'data-short': "false", title: formatFullDate(new Date(post.created_at))}, 
								window.calcTimeFrom(post.created_at, false)
							)
						), 
						
							(post.flags && post.flags.hot)?
							React.createElement("div", {className: "fire", title: "Esse texto é popular."}, 
								React.createElement("i", {className: "icon-whatshot"})
							)
							:null
						
					), 
					React.createElement("div", {className: "body"}, 
						extractTextFromMarkdown(post.content.cardBody || '')
					), 
					React.createElement("div", {className: "footer"}, 
						React.createElement("ul", null, 
							React.createElement("div", {className: "stats"}, 
								React.createElement("div", {className: "likes"}, 
									
										(this.props.model.liked || (this.props.model.get('author').id === (window.user && window.user.id)))?
										React.createElement("i", {className: "icon-heart5 red"})
										:React.createElement("i", {className: "icon-heart5"}), 
									
									React.createElement("span", {className: "count"}, post.counts.votes)
								)
							), 
							React.createElement("div", {className: "participations"}, 
								React.createElement("i", {className: "icon-insert-comment"}), 
								GenParticipations()
							)
						), 
						React.createElement("ul", {className: "right"}, 
							GenTagList()
						)
					)
				), 
				after
			)
		);
					// <div className="backdrop"></div>
					// <div className="over">
					// 	<div className="likes">
					// 		{
					// 			this.props.model.liked?
					// 			<i className="icon-thumb-up icon-orange"></i>
					// 			:<i className="icon-thumb-up"></i>
					// 		}
					// 		<span className="count">{post.counts.votes}</span>
					// 	</div>
					// </div>
								// <span className="count">{post.counts.children}</span>
	}
});

module.exports.User = React.createClass({displayName: 'User',

  render: function () {
  	var model = this.props.model;
  	// console.log(model.attributes)
  	function gotoPerson () {
  		window.location.href = '/@'+model.get('username');
  	}

    return (
			React.createElement("div", {className: "UserCard"}, 
				
					(!window.user || window.user.id === model.get('id'))?
					null
					:(
						model.get('meta').followed?
						React.createElement("button", {className: "btn-follow", 'data-action': "unfollow", 'data-user': model.get('id')})
						:React.createElement("button", {className: "btn-follow", 'data-action': "follow", 'data-user': model.get('id')})
					), 
				
				React.createElement("div", {className: "user-avatar"}, 
					React.createElement("div", {className: "avatar", style:  {background: 'url('+model.get('avatarUrl')+')'} })
				), 
				React.createElement("div", {onClick: gotoPerson}, 
					React.createElement("div", {className: "name"}, 
						model.get('name')
					), 
					React.createElement("div", {className: "userInfo"}, 
						model.get('profile').location, " ", React.createElement("i", {className: "icon-dot"}), " ", model.get('profile').home
					), 
					React.createElement("div", {className: "userStats"}, 
						React.createElement("li", null, 
							React.createElement("div", {className: "value"}, model.get('stats').following), 
							React.createElement("div", {className: "label"}, "seguindo")
						), 
						React.createElement("li", null, 
							React.createElement("div", {className: "value"}, model.get('stats').followers), 
							React.createElement("div", {className: "label"}, "seguidores")
						), 
						React.createElement("li", null, 
							React.createElement("div", {className: "value"}, model.get('stats').karma), 
							React.createElement("div", {className: "label"}, "pontos")
						)
					)
				)
      )
    );
  }

});
